<template>
  <div class="accidentProtection">
    <p class="accidentProtection-title">แผนความคุ้มครองอุบัติเหตุที่คุณต้องการ</p>
    <div class="tabMain">
      <span :class="defaultMain == index ? 'checkTabMain' : 'uncheckTabMain'" v-for="(item,index) in tabButtonText" :key="index" @click="switchMain(index)">
        {{ item.textName }}
      </span>
    </div>
    <div class="baseMain" v-show="baseMainShow">
      <p>
        <span>1.คุณอยากเตรียมเงินสำรองต่อเดือนไว้เท่าไรเพื่อที่จะคงสภาพคุณภาพชีวิตที่ดีของคุณในปัจจุบัน</span>
        <span class="directionText">(กรณีเกิดอุบัติเหตุ)</span>
        <aia-input type="number" v-model="reserveFunds"></aia-input>
      </p>
      <p>
        <span>2.คุณอยากเตรียมเงินสำรองไว้เท่าไร</span>
        <span class="directionText">(กรณีมีค่าใช้จ่ายเพิ่มเติมสำหรับการพักรักษาตัว เช่น ค่าอุปกรณ์การแพทย์ ค่าพยาบาลพิเศษ)</span>
        <aia-input type="number" v-model="reserveFundsMedical"></aia-input>
      </p>
      <p>
        <span>3. คุณอยากเตรียมเงินสำรองไว้เท่าไร </span>
        <span class="directionText">(กรณีชดเชยรายได้เนื่องจากอุบัติเหตุ)</span>
        <aia-input type="number" v-model="reserveFundsIncome"></aia-input>
      </p>
      <p>
        <span>4.เพิ่มความพร้อม</span>
        <aia-input type="number" disabled :value="addPreparation"></aia-input>
      </p>
      <p class="tag-title"><label>คลิกที่นี่</label>เพื่อดูตัวอย่างค่ารักษาพยาบาลเนื่องจากอุบัติเหตุ</p>
    </div>
    <div class="seniorMain" v-show="seniorMainShow">
      <p>
        <span>1. คุณอยากเตรียมเงินสำรองไว้เท่าไร (กรณีเกิดอุบัติเหตุ)</span>
        <aia-input type="number" v-model="allPreparation"></aia-input>
      </p>
      <p>
        <span>2. คุณเตรียมไว้แล้วเท่าไร</span>
        <aia-input type="number" v-model="existPreparation"></aia-input>
      </p>
      <p>
        <span>3. คุณต้องเตรียมเพิ่มอีก</span>
        <aia-input type="number" disabled :value="addPreparationSort"></aia-input>
      </p>
      <p class="explain-text">*ความคุ้มครองแนะนำ =  3-10 เท่าของรายได้ต่อปี เพื่อให้ครอบครัวสามารถปรับตัวและหารายได้แหล่งอื่นมาทดแทน</p>
      <p class="tag-title"><label>คลิกที่นี่</label>เพื่อดูตัวอย่างค่ารักษาพยาบาลเนื่องจากอุบัติเหตุ</p>
    </div>
  </div>
</template>
<script>
export default {
  nanme:'accidentProtection',
  data(){
    return {
      defaultMain:0,
      baseMainShow:true,
      seniorMainShow:false,
      tabButtonText:[{
        textName : 'แบบมาตรฐาน'
      },{
        textName : 'ยิ่งขึ้น'
      }],
      //Standard  
      reserveFunds:'',
      reserveFundsMedical:'',
      reserveFundsIncome:'',
      //sort
      allPreparation:'',
      existPreparation:''
    }
  },
  methods:{
    switchMain(index){
      this.defaultMain = index;
      if(index == 0){
        this.baseMainShow = true;
        this.seniorMainShow = false;
      }else{
        this.baseMainShow = false;
        this.seniorMainShow = true;
        this.allPreparation = Number(this.reserveFunds) + Number(this.reserveFundsMedical) + Number(this.reserveFundsIncome);
      }
    }
  },
  computed:{
    addPreparation(){
      let addPreparation = Number(this.reserveFunds) + Number(this.reserveFundsMedical) + Number(this.reserveFundsIncome);
      if(addPreparation < 0){
        addPreparation = 0;
      }
      return addPreparation;
    },
    addPreparationSort(){
      let addPreparationSort = this.allPreparation - this.existPreparation;
      if(addPreparationSort < 0){
        addPreparationSort = 0;
      }
      return addPreparationSort;
    }
  }
};
</script>
